*, *::before, *::after{
	padding: 0;
	margin: 0;
	border: 0;
	box-sizing: border-box;
}

a {
    text-decoration: none;
	color: inherit;
}

ul, ol, li {
    list-style: none;
}

img { 
    vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
    font-size:inherit;
    font-weight: inherit;
}

html, body{
	height: 100%;
	line-height: 1;
	font-size: 25px;
	font-style: normal;
	overflow-x: hidden;
}
